.control {
  display: flex;
  flex-wrap: wrap;
}

.control .feedback__item {
  @media (min-width: @vTabletWidth) {
    width: 50%;
  }

  @media (min-width: @vDesktopWidth) {
    width: auto;
    margin-right: 28px;
  }
}

.control__label {
  font-weight: 700;
  font-size: 18px;
  line-height: 1.44;
}
